import { BuilderBaseProps } from '../commonTypes';
import { GapUnits } from '../galleryTypes';

// type of arrayRow is an array of numbers
export type PatternRow = number[];
export interface PatternArray extends Array<PatternRow> {}

export interface PatternObject {
  rows: Row[];
}

export interface Row {
  cells: Cell[];
}

interface Cell {
  proportion: number;
}

export enum CustomGridScrollDirection {
  vertical = 'vertical',
  horizontal = 'horizontal',
}

export interface BuildCustomGridStylesProps extends BuilderBaseProps {
  pattern: PatternObject;
  oppositePattern?: string;
  flip?: boolean;
  mode?: CustomGridScrollDirection;
  horizontalGap?: number;
  horizontalGapUnit?: GapUnits;
  verticalGap?: number;
  verticalGapUnit?: GapUnits;
}
